@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'Montserrat', sans-serif;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App{max-width:100vw;min-height:100vh;background-color:rgba(231,240,226,0.534)}.App .reposList{width:60%;margin:0 auto}.App .reposList .List{margin:0 auto}.App .reposList .List .reposItem{max-width:80%;min-height:100%;border-bottom:1px solid #dbc603;padding:1rem;margin:0 auto;box-sizing:border-box}.App .reposList .List .reposItem h3 #icon{margin-right:0.7rem}.App .reposList .List .reposItem h3 a{text-decoration:none;color:#dbc603;transition:0.5s}.App .reposList .List .reposItem h3:hover a{color:#626468}.App .reposList .loadingHolder{max-width:80%;min-height:230px;border-bottom:1px solid #dbc603;padding:1rem;margin:0 auto;box-sizing:border-box;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.App .reposList .loadingHolder .loadingHolderTitle{width:40%;margin:1rem 0;height:0.9rem;background:linear-gradient(45deg, #626468, #8a8f94, #bcbdbe);border-radius:3px;position:relative;overflow:hidden}.App .reposList .loadingHolder .loadingHolderTitle::after{position:absolute;top:0;left:0;content:'';height:100%;width:70%;background:linear-gradient(45deg, transparent, rgba(231,240,226,0.534), transparent);-webkit-transform:translateX(-70%);transform:translateX(-70%);-webkit-animation:loading 2s infinite;animation:loading 2s infinite}.App .reposList .loadingHolder .loadingHolderContent{width:80%;margin:0.3rem 0;height:0.9rem;background:linear-gradient(45deg, #626468, #8a8f94, #bcbdbe);border-radius:3px;position:relative;overflow:hidden}.App .reposList .loadingHolder .loadingHolderContent::after{position:absolute;top:0;left:0;content:'';height:100%;width:70%;background:linear-gradient(45deg, transparent, rgba(231,240,226,0.534), transparent);-webkit-transform:translateX(-70%);transform:translateX(-70%);-webkit-animation:loading 2s infinite;animation:loading 2s infinite}.App .reposList .loadingHolder .loadingHolderContent:last-child{width:60%}@-webkit-keyframes loading{100%{-webkit-transform:translateX(170%);transform:translateX(170%)}}@keyframes loading{100%{-webkit-transform:translateX(170%);transform:translateX(170%)}}@media screen and (min-width: 769px) and (max-width: 1025px){.App .reposList{width:60%}}@media screen and (min-width: 481px) and (max-width: 768px){.App .reposList{width:70%}}@media screen and (max-width: 480px){.App .reposList{width:90%}}

